<template>
	<div class="search">
		<headC></headC>

		<section class="shop-list" 
				v-infinite-scroll="loadMore"
	     		infinite-scroll-disabled="busy"
	     		infinite-scroll-distance="60"
	     		>
			
				<alloy-finger class="shop-item" v-if="" v-for="(item,index) in shopArray" @tap="toGo(index)">
    					<div class="new"><span>新店</span></div>
						<div class="left-cont">
							<img v-bind:src="item.shopimg" alt="">
						</div>
						<div class="right-cont">
							<div class="line">
								<div class="shopname brand">{{item.shopname}}</div>
								<div class="support">
									<div class="activity">
										<i class="activity-icon" v-for="act in item.shopsup">{{act}}</i>
									</div>
								</div>
							</div>
							<div class="line">
								<div class="rate-wrap">
									<span class="rate">{{item.shoprate}}</span>
									<span>月售{{item.shopsale}}单</span>
								</div>
								<div class="delivery-wrap">
									<div class="bird">蜂鸟专送</div>
									<div class="ontime">准时达</div>
								</div>
							</div>
							<div class="line">
								<div class="money-wrap">
									<span>${{item.shopfee}}起送</span>
									<span>配送费{{item.delifee}}元</span>
								</div>
								<div class="time-wrap">
									<span>{{item.distance}}</span>
									<span>{{item.time}}分钟</span>
								</div>
							</div>
						</div>
					
				</alloy-finger>
			</section>
	</div>
	

</template>
<script>

	import {getShopItemList,getFeatItemList} from '../vuex/shopitem/siActions'
	import headC from '../components/search-head'
	export default{
		data(){
			return {
				shopArray:[],
				shopfeat:[]
			}
		},
		components:{
			headC
		},
		mounted(){
			this.getListData()
			.then(()=>{
				this.shopArray=this.$store.getters.getList.slice(0,10)
			})
			
		},
		methods:{
			loadMore: function () {
  				if(this.shopArray.length!==0){
  					var shopitem=[]
			  	for(var i=0;i<4;i++){
				    shopitem.push(this.$store.getters.getList[_.random(0, 59)])
				}
				this.shopArray=this.shopArray.concat(shopitem)
  				}
			  	
		    },
		    getShopItemList,
			getListData() {
                return this.getShopItemList()
                    .then((data) => {
                        this.$store.dispatch('setList',data)
                    })
                    .catch((err) => {
                         console.log("error")
                    })
            },
            toGo:function(index){
				this.$router.push({ name: 'detail', params: { id: index }})
			},
		}
	}
</script>
<style lang="scss" scoped>
	.search{
		padding-top: 1.16rem;
		.my-swipe {
		  height: 4.72rem;
		  color: #fff;
		}
		.category{
			height: 4.72rem;
			border-bottom: 1px solid #ddd;
    		text-align: center;
    		background: #fff;
		}
		.item{
		  		width: 25%;
				float: left;
				color: #666;
				height: 2.2rem;
				display: block;
				padding-top: 0.2rem;
				.item__img{
					width: 1.2rem;
					height: 1.2rem;
					display: inline-block;
					img{
						width: 100%;
						height: 100%;
					}
				}
				.item__title{
					font-size: 0.32rem;
				}
		  	}
		.index-title{
			margin-top: 0.266667rem;
			border-bottom:1px solid #F7F7F7;
			line-height: 0.906667rem;
			color: #999;
			font-size: .346667rem;
			text-align: left;
			padding-left: .4rem;
    		background: #fff;
    		i{
    			font-size: .42rem;
    			padding:.1rem;
    		}
		}
		.shop-item{
			position:relative;
			padding:.4rem .266667rem;
			background:#fff;
			border-bottom: 1px solid #eee;
			display: flex;
			justify-content:space-between;
			&>a{
				display: flex;
				width: 100%;
				height: 100%;
			}
			.left-cont{
				width: 1.6rem;
				height: 1.6rem;
				margin-right: 0.266667rem;
				img{
					width: 100%;
					height: 100%;
				}
			}
			.right-cont{
				flex-grow:1;
				font-size: .293333rem;
				color: #666;
			}
		}
	}
	.line{
		    display: flex;
		    justify-content:space-between;
		    text-align: left;
		    .activity-icon{
				color: rgb(153, 153, 153);
    			border:1px solid #ccc;
    			font-style: normal;
    			border-radius:1px;
		    }
		    .activity-icon:nth-child(2){
		    	margin-left: 0.1rem;
		    }
		    span:nth-of-type(2):before{
			    margin: 0 .08rem;
		    color: #ddd;
		    content: '/';
		}
	}
	.line:nth-child(2){
		margin-top: .2rem;
	}
	.line:nth-child(3){
		margin-top: 0.226667rem;
		line-height: .32rem;
	}
	.shopname{
		    overflow: hidden;
		    margin: 0;
		    padding: 0;
		    max-width: 5rem;
		    color: #333;
		    text-overflow: ellipsis;
		    white-space: nowrap;
		    font-weight: 700;
		    font-size: .4rem;
		    line-height: .426667rem;
	}
	.brand:before{
		    margin-right: .133333rem;
		    padding: 0 .066667rem;
		    height: .4rem;
		    border-radius: .053333rem;
		    background-color: #ffd930;
		    color: #52250a;
		    content: "\54C1\724C";
		    vertical-align: top;
		    text-align: center;
		    font-weight: 700;
		    font-size: .293333rem;
		    line-height: .4rem;
	}
	.rate{
		color: #ff6000;
		margin: 0 .106667rem;
	}
	.delivery-wrap{
		display: flex;
		div{
			padding: 0 .053333rem;
		    border: 1px solid #44a5ff;
		    border-radius: .053333rem;
		    background-color: #fff;
		    color: #2395ff;
		    font-size: .266667rem;
		    line-height: .4rem;
		}
		.bird{
			margin-right: .08rem;
			background-color: #2395ff;
    		color: #fff;
		}
	}
	.money-wrap{
		span:nth-of-type(2) {
		    max-width: 3.333333rem;
		    overflow: hidden;
		    text-overflow: ellipsis;
		    white-space: nowrap;
		}
	}
	.time-wrap{
		span:nth-of-type(1){
			color: #999;
		}
		span:nth-of-type(2){
			color: #2395ff;
		}
	}
	.new{
		width: 0.853333rem;
		height: 0.853333rem;
		position:absolute;
		top: 0;
		left: 0;
		background:linear-gradient(135deg,#26ce61,#26ce61 50%,transparent 0);
		z-index: 100;
		text-align: center;
		span{
			color: #fff;
			line-height: 0.3rem;
			transform: rotate(-45deg) translateX(-.2rem);
			display: block;
			font-size: 0.24rem;
			font-weight: 700;
		}
	}
</style>



